* {
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;

  font-size: 20px;
}

.title {
  margin-top: 10px;
  font-size: 40px;
  color: black;
}

.App {
  text-align: center;
  font-size: 80px;
  border: 1px black solid;
  width: 500px;
  text-align: center;
  margin-left: 30%;
  margin-top: 5%;
  height: auto;
  padding: 50px;
  border-radius: 20px;
}

.input {
  border: 5px black solid;
  height: 30px;
  margin-top: 20px;
  width: 300px;
}

.add {
  color: white;
  background-color: turquoise;
  height: 40px;
  width: 80px;
  margin: 5px;
}

.item {
  text-decoration: none;
  list-style: none;
  margin: 5px;
  border: 5px black solid;
  font-size: 80;
  padding: 10px;
  border-radius: 20px;
  height: auto;
  width: auto;
}

.delete {
  background-color: white;
  color: rgb(214, 64, 64);
  border: 3px rgb(214, 64, 64) solid;
  font-size: 80;
  border-radius: 20px;
  padding: 20px;
  margin-left: 100px;
}
